import React, {Component} from 'react';

class Form extends Component {
    constructor(porps) {
        super(porps);

        this.state = {
            inputValue: '',
            isOpen: false,
        };

    }

    render() {

        const { isOpen } = this.state;

        return (
            <form className="form">
                <div className="form__group form-group">
                    <input type="text"
                           className="form__input"
                           onChange={this.props.getInputValue}
                           placeholder="Search"/>
                    <button className="form__button button"
                            onClick={this.toggle}
                            type="button">
                        {isOpen ? 'Close' : 'Open'}
                    </button>
                </div>
                {this.getBlock()}
            </form>
        )
    }

    toggle = () => {
        this.setState({
            isOpen: !this.state.isOpen,
        });
    }

    getBlock() {
        if(!this.state.isOpen) return null;

        return <section>Test Msg</section>
    }

}

export default Form;


