import React, {Component} from 'react';
import Form from '../form/forms';
import { Breadcrumbs } from './breadcrumbs';


class Header extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
                <header className="header">
                  <Breadcrumbs listItems={this.setBreadcrumbs()}/>
                    <Form getInputValue={this.getInputValue}
                          searchOnClick={this.search}
                          buttonTxt='Search' />
                </header>
        );
    }

    setBreadcrumbs(){
        const testBreadcrumbs = ['test', 'menu', 'second menu'];
        testBreadcrumbs.map(el=>console.log(el.slice(0,1)));
        return testBreadcrumbs;
    }
}

export default Header;



