import React, { Component } from 'react';


 const Breadcrumbs = ({ listItems }) => (
    <ul className="breadcrumbs">
        {listItems
            .map((item,key) => <li
                key={key} className={`${listItems.length[key - 1] ? 'breadcrumbs__item breadcrumbs__item--active' : 'breadcrumbs__item'} `}> {item} </li>)}
    </ul>
);

 export { Breadcrumbs };



